import { addEmployee, 
         deleteUser, 
         setDateOfBirth, 
         setEmployeeManager, 
         searchEmployees, 
         getElementemployeesPlaceholder,
         getElementplaceholderERROR,
         getDATA,
         getManagers,
        } 
from './service';
import Employee, {jsonToEmployees} from "./model/Employees";

//Отображение списка сотрудников
//--------------------------------------------------------------
function showEmployees(employees, selectorToAdd) {   
  let data = jsonToEmployees(employees);
  let newlist = employeesView(data)
  newlist.forEach(li => document.querySelector(`#${selectorToAdd}`).append(li));
};
function employeesView(employees) {
  let list = [];
  function createElementLI(item) {
    let li = document.createElement('li');
    let button = document.createElement('button');
    let select = document.createElement('select');

    li.classList.add('user');
    li.id = item.id;
    li.innerHTML = item;

    button.classList.add('btn');
    button.id = `deleteUser${item.id}`;
    button.innerText = 'x';
    button.addEventListener('click', () => removeEmployeeUI(item.id))

    select.classList.add('users__managerSelect');
    select.id = `selectUser${item.id}`;
    select.addEventListener('change', () => setEmployeeManager(item.id, ));
    
    li.prepend(button);
    !item.managerRef ? false : li.append(fillSelect(select, getManagers(), item.managerRef));
    return li
  }
  employees.forEach(item => list.push(createElementLI(item)))
  return list;
}
function showEmptyListUser() {
  let list = document.querySelectorAll('.user');
  if (list.length === 0) {
    let el = getElementemployeesPlaceholder();
    el.innerText = 'список пуст';
  }
}
//--------------------------------------------------------------


//Добавление нового сотрудника
//--------------------------------------------------------------
export function addEmployeeUI() {
  const name = document.querySelector('#name');
  const surname = document.querySelector('#surname');
  const birthDate = document.querySelector('#birthDate');
  const manager = document.querySelector('#managerSelect').value;

  addEmployeeUIView(name, surname, manager, birthDate)
  runUI();
};
function addEmployeeUIView(name, surname, manager, birthDate) {
  chekInputsValue(name, surname, birthDate);
  setDateOfBirth(addEmployee(name.value, surname.value, manager), birthDate.value);
  name.value = '';
  surname.value = '';
  birthDate.value = '';
};
function chekInputsValue(name, surname, birthDate) {
  document.querySelectorAll('.userData').forEach( item => {
    item.classList.remove('checkFailed');
  })

  if(!name.value || !surname.value || !birthDate.value) {
    if(!name.value) name.classList.add('checkFailed');
    if(!surname.value) surname.classList.add('checkFailed');
    if(!birthDate.value) birthDate.classList.add('checkFailed');
    addEmployeeFormErrorMessage('Одно или несколько полей пустые. Проверьте корректность вводимых данных.');

    setTimeout(() => {
      clearEmployeeFormErrorMessage();
      if(!name.value) name.classList.remove('checkFailed');
      if(!surname.value) surname.classList.remove('checkFailed');
      if(!birthDate.value) birthDate.classList.remove('checkFailed');
    }, 3000);
    return false;
  }
};
function fillSelect(element, array, currentValue) {
  array.forEach( item => {
    let option = document.createElement('option');
    option.innerText = item.text;
    option.id = item.value;
    (currentValue.toString()===item.value.toString()) ? option.selected = 'selected' : false
    element.append(option);
  })
  return element
};
//--------------------------------------------------------------


//Удаление сотрудника из списка
//--------------------------------------------------------------
function removeEmployeeUI(id) {
  deleteUser(id);
  runUI();
  showEmptyListUser();
};
//--------------------------------------------------------------


//Очистка и создание новых Placeholder
//--------------------------------------------------------------
function clearEmployeesPlaceholder() {
  let el = getElementemployeesPlaceholder();
  el.innerHTML = '';
};
function clearEmployeeFormErrorMessage() {
  let el = getElementplaceholderERROR();
  el.innerHTML = '';
};
function addEmployeesPlaceholder(message) {
  let el = getElementemployeesPlaceholder();
  el.innerText = message;
  let ul = document.createElement('ul');
  
  ul.classList.add('user__list');
  ul.id = 'listUser';
  el.append(ul);
};
function addEmployeeFormErrorMessage(message) {
  let el = getElementplaceholderERROR();
  el.innerText = message;
};
//--------------------------------------------------------------


//Переключение закладок tab
//--------------------------------------------------------------
export function openTab(evt, id) {
  let tabId = evt.target.id;
  let tabs = document.querySelectorAll('.tab');
  let tab = document.querySelector(`#${tabId}`);
  let tabcontent = document.querySelectorAll('.users__tabcontent');
  let pane = document.querySelector(id);
  
  tabcontent.forEach( item => item.classList.remove('active'));
  tabs.forEach( item => item.classList.remove('activeTab'));

  (pane.classList.contains('active')) ? false : pane.classList.add('active');
  (tab.classList.contains('activeTab')) ? false : tab.classList.add('activeTab');
};
//--------------------------------------------------------------


//Поиск сотрудников
//--------------------------------------------------------------
export function searchEmployeeUI() {
  clearEmployeesPlaceholder();
  let elementsForm = document.forms.searchEmployees.elements;
  let name = elementsForm.name.value;
  let surname = elementsForm.surname.value;
  let managerRef = elementsForm.manager.selectedOptions[0].id;

  let result = searchEmployees(name, surname, managerRef)
  if(result === false) {
    addEmployeesPlaceholder('Поиск не дал результатов, уточните критерии поиска');
  } else {
    addEmployeesPlaceholder('');
    showEmployees(result, 'listUser');
  }
};
//--------------------------------------------------------------


//Запуск поиска и его привязка к enter
//--------------------------------------------------------------
function assignSendOnEnter(paneId, buttonId) {
  let rootElement = document.querySelector(`#${paneId}`)
  let inputsElements = rootElement.querySelectorAll('input');
  inputsElements.forEach( el => {
    el.addEventListener('keyup', () => send(buttonId));
  })
};
function send(buttonId) {
  if(event.keyCode === 13){ //убрал event.keyCode === 13
    let button = document.querySelector(`#${buttonId}`);
    button.click();
  };
};
//--------------------------------------------------------------


//Запуск UI
//--------------------------------------------------------------
export function runUI() {
  document.querySelector('#addTab').addEventListener('click', () => openTab(event, '#addPane'));
  document.querySelector('#searchTab').addEventListener('click', () => openTab(event, '#searchPane'));
  document.querySelector('#searchButton').addEventListener('click', searchEmployeeUI);
  document.querySelector('#addUser').addEventListener('click', addEmployeeUI);

  assignSendOnEnter('addPane', 'addUser');
  assignSendOnEnter('searchPane', 'searchButton');
  clearEmployeesPlaceholder();
  addEmployeesPlaceholder('');
  showEmployees(getDATA(), 'listUser');
  if (document.querySelector('#managerSelect').length===0) fillSelect(document.getElementById('managerSelect'), getManagers(), '1');
  if (document.querySelector('#managerSearch').length===0) fillSelect(document.getElementById('managerSearch'), getManagers(), '1');
}; 
//--------------------------------------------------------------