import DATA, { employeeMap, managers, PLACEHOLDER, placeholderERROR} from '../../employees-json';


//Добавление сотрудника в DATA
//--------------------------------------------------------------
export function addEmployee(name, surname, manager) {
  try {
    if(!name || !surname) throw new Error('name and surname should be not empty');
    
    let id = getNewID(getDATA());
    let managerId = getManagerId(manager);

    for(let [_, value] of Object.entries(DATA)) {
      value.push({id, name, surname, department: ''});
    };

    setEmployeeManager(id, managerId)
    return id;
  } catch(err) {
    return err;
  };
};

export function setEmployeeManager(id, managerId) {
  //вызывается если на вход managerId неизвестен, но можем получить ФИО менеджера
  if(!managerId) {
    const manager = document.querySelector(`#selectUser${id}`).value;
    managerId = getManagerId(manager);
  }
  //штатный рендер списка руководителей при добавлении нового сотрудника
  let e = findById(id)
  e.managerRef = managerId;
};

function getNewID(employees) {
  let max = 0;
    for (let e of employees) {
      if (e.id>max) max = e.id;
    }
  let id = max+1;
	return id;
}

function getManagerId (manager) {
  for (let e of managers) {
    if (`${e.text}` === manager) return e.value;
  }
};
//--------------------------------------------------------------


//Удаление сотрудника из DATA
//--------------------------------------------------------------
export function deleteUser(id) {
  for(let [_, value] of Object.entries(DATA)) {
    for (let i = 0; i < value.length; i++) {
      if(value[i].id === id) {
        value.splice(i,1);
        break;
      } 
    }
  }
  return DATA
};
//--------------------------------------------------------------


//Установка дня рождения сотрудника в DATA
//--------------------------------------------------------------
export function setDateOfBirth(id, date) {
  let employee = findById(id);
  if (!employee) {
    return 'Wrong id';
  };
  employee.dateOfBirth = date.toString();
  return employee;
};

function findById(id) {
  if(!id) {
    return 'id is empty'
  }
  
  if(employeeMap[id]) employeeMap[id];
  let data = getDATA();
  for (let e of data) {
      if (e.id === id) { 
        employeeMap[id] = e;
        return e;
      }
  }
};
//--------------------------------------------------------------


//Поиск сотрудника в DATA
//--------------------------------------------------------------
export function searchEmployees(name, surname, managerRef) {
  let result = new Set();
  let resultSearchByName = findByName(name, surname);
  let resultSearchByManager = findByManagerRef(managerRef);
  
  resultSearchByName.forEach(item => result.add(item))
  resultSearchByManager.forEach(item => result.add(item))
  
  if (resultSearchByName.length === 0 && resultSearchByManager.length ===0) return false;
  return result;
};

function findByName(name, surname) {
  for(let [_, value] of Object.entries(DATA)) {
    if (!name && !surname) return value;
    if (name && !surname) return value.filter(item => item.name === name);
    if (!name && surname) return value.filter(item => item.surname === surname);
    let result = value.filter(item => item.name === name && item.surname === surname);
    return result;
  };
};

function findByManagerRef(managerRef) {
  for(let [_, value] of Object.entries(DATA)) {
    let managers = value.filter(item => item.managerRef == Number(managerRef));
    return managers;
  };
};
//--------------------------------------------------------------


//Генераця списка руководителей в отдельный массив managers
//--------------------------------------------------------------
(function createManagersArray() {
  for (let e of getDATA()) {
    managers.push({
      text: `${e.name} ${e.surname}`,
      value: `${e.id}`
    })
  };
})();
//--------------------------------------------------------------


//Вспомогательные функции
//--------------------------------------------------------------
//Возвращает элемент плейсхолдер
export function getElementemployeesPlaceholder() {
  return document.querySelector(`#${PLACEHOLDER}`);
};
//Возвращает элемент плейсхолдер для записи ошибки
export function getElementplaceholderERROR() {
  return document.querySelector(`#${placeholderERROR}`);
};
//Возвращает DATA.employees
export function getDATA() {
  return DATA.employees;
}
//Возвращает массив менеджеров/руководителей для списка select-option
export function getManagers() {
  return managers;
}
